import { getUserInitials } from "@rp/common/utils"
import { Avatar } from "@rp/react/web/mui/core"

type Props = {
  ownerName?: string
  imgUrl?: string
  isLoading?: boolean
  hasImage?: boolean
  size?: number
}

const DEFAULT_SIZE = 35

const UserAvatar = ({ ownerName, imgUrl, isLoading, hasImage, size = DEFAULT_SIZE }: Props) => {
  const userInitials = ownerName ? getUserInitials(ownerName) : undefined
  return (
    <Avatar
      sx={{
        backgroundColor: "overlay.surface",
        color: "text.secondary",
        fontSize: size / DEFAULT_SIZE + "rem",
        lineHeight: DEFAULT_SIZE / size,
        width: `${size}px`,
        height: `${size}px`,
        "& img": {
          objectFit: "cover",
          width: "100%",
          height: "100%",
        },
      }}
    >
      {imgUrl && !isLoading && hasImage ? <img src={imgUrl} alt={ownerName} /> : userInitials}
    </Avatar>
  )
}

export { UserAvatar }
